@font-face {
    font-family: 'handtext';
    src: url('./font/handtext.ttf') format('truetype');
}

@keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@-webkit-keyframes fadein {

    /* Safari and Chrome */
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.container {
    animation: fadein 1s;
    background-color: #222222;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.memo {
    animation: fadein 2s;
    border-radius: 50px;
    width: 40%;
    height: 90vh;
    background-color: #fff9a3;
}

header {
    width: 100%;
    height: 100px;
    text-align: center;
    border-bottom: 2px solid black;
}

h2 {
    animation: fadein 3s;
    font-family: 'handtext';
    font-size: 48px;
}

.text {
    animation: fadein 3.5s;
    font-family: 'handtext';
    font-size: 24px;
    font-weight: bold;
    line-height: 60px;
    text-align: center;
}


